<template>
  <div class="iterator">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import ui from "cy_static/ui";
    import iterator from "cy_static/iterator";
    var ins = new iterator();
    // 加载第一个模块
    ins.push(function(status) {
      ui.toast("加载第一个模块");
    })
    // 加载第二个模块---存在异步
    var num = 0;
    ins.push(function(status) {
      setTimeout(function() {
        status.isEnd = num > 5 ? true : "isBack";
      }, 2000)
      ui.toast("加载第二个模块第" + (num++) + "次");
      return false;
    })
    // 模拟触发滚动条
    setInterval(function() {
      ins.next();
    }, 1000);
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">iterator 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import iterator from "cy_static/iterator"; 
&lt;/script&gt;
</xmp >
  
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;

&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>
        <ul class="ul-props">
					<li>
						<p><a>push</a>(func : Function) : <a>Void</a></p>
						<p>添加迭代器加载模块</p>
            <table class="table-props border">
              <tr>
                <th colspan="2">func 函数返回值含义</th>
              </tr>
              <tr>
                <th>返回值</th>
                <th>说明</th>
              </tr>
              <tr>
                <td class="atn">false</td>
                <td>表示没结束执行中(有异步)</td>
              </tr>
              <tr>
                <td class="atn">undefined</td>
                <td>表示该方法已结束(默认)</td>
              </tr>
              <tr>
                <td class="atn">string</td>
                <td>可以进到下一轮(异步已返回)</td>
              </tr>
              <tr>
                <td class="atn">true</td>
                <td>表示该方法已结束(主动结束)</td>
              </tr>
            </table>
					</li>
					<li>
						<p><a>next</a>() : <a>Object</a></p>
						<p>加载迭代模块，返回迭代状态对象</p>
            <table class="table-props border">
              <tr>
                <th colspan="2">代状态对象含义</th>
              </tr>
              <tr>
                <th>key</th>
                <th>类型</th>
                <th>说明</th>
              </tr>
              <tr>
                <td>value</td>
                <td class="atn">Function</td>
                <td>当前函数</td>
              </tr>
              <tr>
                <td>done</td>
                <td class="atn">Boolean</td>
                <td>模块是否已加载完</td>
              </tr>
              <tr>
                <td>isEnd</td>
                <td class="atn">Boolean</td>
                <td>当前模块加载完成状态</td>
              </tr>
            </table>
					</li>
					<li>
						<p><a>end</a>() : <a>Void</a></p>
						<p>主动设置当前模块迭代完成</p>
					</li>
				</ul>
      </div>




    </div> 
  </div>
</template>

<script>
import {showDetail, trim} from "../../../assets/js/codeStyle";
export default {
  name: 'iterator',
  data () {
    return {
      msg: 'iterator',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .cy_example .codeArea{height:435px}
  .cy_example .iframeBox{flex:10}
</style>



